import React from "react";
import { Segment } from "semantic-ui-react";
import { graphql } from "gatsby";
import qs from "qs";
import Layout from "../components/Layout";
import { List, Pagination } from "../components/Post";

export default function ({ data }) {
  const { nodes } = data.allMarkdownRemark;
  const search =
    typeof document === `undefined` ? "" : document.location.search;
  const { tag = "", page = "1" } = qs.parse(search, {
    ignoreQueryPrefix: true,
  });

  const posts = nodes.filter((node) =>
    tag ? node.fields.tags.includes(tag) : true
  );
  const totalPosts = posts.length;
  const postsPerPage = 10;
  const totalPages = Math.ceil(totalPosts / postsPerPage);
  const skip = (parseInt(page) - 1) * postsPerPage;

  return (
    <Layout>
      <Segment basic as="section">
        <List
          title={tag || "All"}
          totalPosts={totalPosts}
          posts={posts.slice(skip, skip + postsPerPage)}
        />
        <Segment basic textAlign="center">
          <Pagination totalPages={totalPages} tag={tag} page={page} />
        </Segment>
      </Segment>
    </Layout>
  );
}

export const query = graphql`
  query listPosts {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt(truncate: true)
        timeToRead
        fields {
          slug
          tags
        }
        frontmatter {
          title
          date(formatString: "MMM D, YYYY")
        }
      }
    }
  }
`;
